body {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 20px;
}
div {
    font-size: 30px;
}

.weather h1 {
    font-size: 30px;
    margin-bottom: 40px;
    -webkit-animation: none;
            animation: none;
}

.weather {
    margin-bottom: 60px;
    background-color: rgb(95, 158, 160);
    height: auto;
    width: 30vw;
    border-radius: 4%;
}

.weather div:last-child {
    margin-bottom: 50px;
}
.app {
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

h1 {
  text-transform: uppercase;
  font-size: 60px;
  margin-top: 50px;
  margin-bottom: 100px;
  -webkit-animation: blinkin 1s linear infinite;
          animation: blinkin 1s linear infinite;
}

@-webkit-keyframes blinkin {
  0% {
    color: black
  }

  50% {
    color: rgba(0, 0, 0, 0);
  }

  100% {
    color: black;
  }
}

@keyframes blinkin {
  0% {
    color: black
  }

  50% {
    color: rgba(0, 0, 0, 0);
  }

  100% {
    color: black;
  }
}
